	module.exports = {"data":{"wordpressPost":{"title":"Open roads and closed sessions","content":"<p> </p>\n<p>A recent report on the next steps for increasing open access to UK research concluded that Green OA infrastructure (i.e. repositories) should be encouraged while an economically sustainable transition to Gold OA is worked through. </p>\n<p>‘Heading for the open road: costs and benefits of transitions in scholarly communications’ by CEPA and Mark Ware Consulting, was commissioned by the Research Information Network, the Wellcome Trust and others, and was released a few weeks ago.</p>\n<p>The report assesses various ways to increase access to scholarly communications (i.e. journal articles) emanating from researchers based at UK institutions.</p>\n<p>The authors produce a benefit—cost ratio for each scenario and discuss the relative risks, including risks to the publishing industry’s business models, acknowledging the value that the industry brings over and above the administration of peer review.</p>\n<p>The report concludes that the most cost-effective scenario is the ‘delayed’ scenario, where articles are made freely available after an embargo period. As publishers retain control of this embargo period, subscription cancellations are thought to be less likely, and the set-up costs for such a transition are seen to be low. The embargo periods would quite probably be longer than preferred by funders, though, meaning that the idea is unlikely to gain traction. Another premise is that the preferred scenario should be one open to influence by public and funders’ policies, and the current run of mandates for repository deposition casts further doubt on this model’s success.</p>\n<p>So the report concludes as follows.</p>\n<p>‘[O]ur view is that the prudent stance for policy-makers seeking to promote access in the current environment is likely to be as follows:</p>\n<p>to encourage the use of existing Green infrastructure [i.e. repositories] (whose costs are largely sunk);</p>\n<p>but to be cautious about pushing for reductions in embargo periods to the point where the sustainability of the underlying publishing model is put at risk;<br />\nin parallel, to work to facilitate a transition to Gold OA (in specific disciplines first) provided that (i) the average level of APCs [article-processing charges] remain at or below £1,995; (ii) the proportion of articles funded through APCs moves broadly in line with global rates; and (iii) mechanisms are in place to ensure that total payments from UK universities and their funders do not rise as a consequence of this transition.’</p>\n<p>In a reply posted in various parts of the blogosphere Green OA advocate Stevan Harnad has branded the second part of the report’s conclusions short-sighted, premature and mistaken. You can download the report, read his views and the response from RIN’s Michael Jubb, <a href=\"http://www.rin.ac.uk/our-work/communicating-and-disseminating-research/heading-open-road-costs-and-benefits-transitions-s\" target=\"_blank\">here</a>.</p>\n<p>In his response to Harnad, Jubb argues that ‘it is perverse not to recognise that the stakes are high for individual publishers and perhaps for the industry as a whole’. What is at stake was under discussion at the PA–ALPSP journal publishers’ forum on ‘<a href=\"http://www.alpsp.org/ngen_public/article.asp?aid=341706\" target=\"_blank\">Open access: the next 10 years</a>’. The event was held under the <a href=\"http://www.chathamhouse.org.uk/about/chathamhouserule/\" target=\"_blank\">Chatham House rule</a> so comments can’t be attributed. There was a cautious approach, reflected in the session&#8217;s premises (&#8216;Has the time come to turn the threat into an opportunity?&#8217;). There was a focus on: getting to grips with the current trajectory of public sector and funding bodies’ open access policies, rather than crystal-ball gazing to predict the state of play in a decade’s time; the rise of Green OA mandates; and the perceived need for a &#8216;compelling, coherent and above all positive story&#8217; to tell about the value the scholarly publishing industry brings.</p>\n<p>A more formal output is promised.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"e775a73f-8807-5375-b5ce-aaa06a663538"}}

